<template lang="jade">
  .login-register
    div.form-item
      label.item.ds-input :
        input(v-model="account" placeholder="")
    div.form-item
      label.item.ds-input :
        input(v-model="name" placeholder="")
    div.form-item
      label.item.ds-input :
        input(style="width: 1rem" v-model="code_" @keyup.enter="autoRegist" placeholder="")
        i.ds-icon-code(v-bind:style="{background: 'url(' + img_ + ') right center no-repeat'}" @click="_getVerifyImage")

    div.form-item
      .ds-button.bold.tall.high-positive._3rem.oval(@click="autoRegist") 
      br
      router-link.ds-button.text-button.light(:to=" '/login' " style="margin-top: .1rem") 




    
</template>

<script>
  // import '../../main.all.out.min.js'
  import api from '../../http/api'
  import browser from '../../util/browser'
  import xhr from 'components/xhr'
  import Validate from '../../util/Validate'
  export default {
    mixins: [xhr],
    components: {
      // SignalBar
    },
    data () {
      return {
        account: '',
        name: '',
        tag: ''
      }
    },
    computed: {
    },
    mounted () {
      this._getVerifyImage()
      this.tag = this.$route.query.tag
      if (browser.mobile) window.location.href = 'http://mobile.cagames.ca.go?tag=' + this.tag
      this.getStrangerInfo()
    },
    beforeDestroy () {
      window.accessAngular.close()
    },
    methods: {
      getStrangerInfo () {
        this.$http.post(api.getStrangerInfo, {
          tag: this.tag
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            window.accessAngular.setUser({
              id: data.strangerId,
              key: data.token,
              pltCd: data.platId,
              socketUrl: data.platUrl,
              toId: data.userId
            })
            window.accessAngular.isStranger(true)
            window.accessAngular.connect()
          } else this.$message.warning(data.msg || ' !')
        }, (rep) => {
          // error
          this.$message.warning(' !')
        })
      },
      // http://192.168.169.44:9901/cagamesclient/team/createAccount.do?method=autoRegist&tag=7F593EF2F9B3537291FF912CAA7C49A5&userName=test123&nickName=test123&verifyCode=4953
      autoRegist () {
        if (!this.account) return this.$message.warning({target: this.$el, message: ''})
        if (!Validate.account(this.account)) return this.$message.warning({target: this.$el, message: '0-9a-zA-Z6-16'})
        if (!this.name) return this.$message.warning({target: this.$el, message: ''})
        if (!Validate.nickName(this.name)) return this.$message.error({target: this.$el, message: '284QQ'})
        if (!this.code_) return this.$message.error({target: this.$el, message: ''})
        this.$http.post(api.autoRegist, {
          tag: this.tag,
          userName: this.account,
          nickName: this.name,
          verifyCode: this.code_
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.$modal.success({
              // target: this.$el,
              content: '123qwe',
              btn: [''],
              ok () {
                this.__setCall({
                  fn: '__logout',
                  args: {
                    stay: true
                  }
                })
                this.$router.push({
                  path: '/login/login',
                  query: {
                    un_: this.account,
                    pwd: '123qwe'
                  }
                })
              },
              O: this
            })
          } else this.$message.warning(data.msg || '!')
        }, (rep) => {
          // error
          this.$message.warning('!')
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  W = 2.7rem
  H = 1.8rem
  .login-register
    background url(../../assets/banner.png) 55% top no-repeat
    padding-top 4rem
    position relative
    top -1.5rem
    text-align center
    text-shadow none

  .form-item
    margin PW 0
  .item
    position relative
    width 3rem
    height .5rem
    padding 0 .25rem
    line-height .5rem
    radius(.25rem)
    color #bbb
    text-align left
    input
      border none
      padding-left .1rem
      width 2rem
  .ds-icon-code
    position: absolute;
    bottom: 0.1rem;
    right: 0.15rem;
    display: inline-block;
    width: 0.83rem;
    height: 0.29rem;
    cursor: pointer; 

    
</style>


